import React from 'react';
import "./Movie.css"
const Movie = (props) => {
    return (
        
            <div className="card movie-card" >

                {props.image = <img className="card-img-top" src={`https://image.tmdb.org/t/p/w500${props.image}`}
                    alt="Card image cap" />}
                <div className="card-body">
                    <h5 className="card-title"></h5>
                    <p className="card-text"></p>
                </div>

                    




            </div>
      
    )
}

export default Movie;